import {
    useEffect,
    useRef,
    useState,
} from 'react';

export const isFalsy = (value: unknown) => value === 0 ? false : !value;

export const isVoid = (value: unknown) => value === undefined || value === null || value === '';

export const cleanObject = (object: {[key: string]: unknown}) => {
    const result = {...object};
    Object.keys(result).forEach((key) => {
        const value = result[key];
        if (isVoid(value)) {
            delete result[key];
        }
    });
    return result;
};

/**
 * @description 组件渲染完之后执行相关回调
 * */ 
export const useMount = (callback: () => void) => {
    useEffect(() => {
        callback();
    }, [callback]);
};

/**
 * @description 防抖
 * @param {Any} value 需要防抖的参数
 * @param {Number} delay 时间间隔
 * */ 
export const useDebounce = <V>(value: V, delay?: number) => {
    const [debouncedValue, setDebouncedValue] = useState(value);
    useEffect(() => {
       const timeout = setTimeout(() => setDebouncedValue(value), delay); // 每次value变化之后设置一个定时器
       return () => clearTimeout(timeout); // useEffect中return出来的函数会在上一次useEffect的回调函数执行完之后才会执行，
    }, [value, delay]);
    return debouncedValue;
};

export const useArray = <T>(initialArray: T[]) => {
    const [value, setValue] = useState(initialArray);
    return {
        value,
        setValue,
        add: (item: T) => setValue([...value, item]),
        clear: () => setValue([]),
        removeIndex: (index: number) => {
            const copy = [...value];
            copy.splice(index, 1);
            setValue(copy);
        }
    }
};

// 改变文档标题
export const useDocumentTitle = (title: string, keepOnUnmount: boolean = true) => {
    const oldTitle = useRef(document.title).current;
    useEffect(() => {
        document.title = title;
    }, [title]);
    useEffect(() => {
        return () => {
            if (!keepOnUnmount) {
                document.title = oldTitle;
            }
        }
    }, [keepOnUnmount, oldTitle]);
};

/**
 * @description 重置路由
 * */ 
export const resetRoute = () => {
    window.location.href = window.location.origin;
}

/**
 * 用于返回组件的挂载状态，如果还没挂载或已经卸载，返回false,否则返回true
 * */ 
export const useMountedRef = () => {
    const mountedRef = useRef(false);
    useEffect(() => {
        mountedRef.current = true;
        return () => {
            mountedRef.current = false;
        }
    });
    return mountedRef;
}

