import { QueryKey, useMutation, useQuery } from "react-query";
import { Task } from "types/task";
import { useHttp } from "./http";
import { SortProps } from "./kanban";
import { useAddConfig, useDeleteConfig, useEditConfig, useReorderTaskConfig } from "./use-optimistic-options";

/**
 * @description 获取看板列表
 * @param param 查询参数
 * */ 
export const useTasks = (param?: Partial<Task>) => {
    const client = useHttp(); // fetch请求
    return useQuery<Task[]>(['tasks', param], () => // n秒之内如果有相同queryKey发送，那么ReactQuery会将这些相同query合并为一个请求进行发送
        client('tasks', { data: param }) // 当param变化的时候会重新触发useQuery
    );
};

/**
 * @description 添加任务
 * @param queryKey key
 * */ 
export const useAddTask = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        (params: Partial<Task>) => client('tasks', {
            method: 'POST',
            data: params,
        }),
        useAddConfig(queryKey)
    );
};

/**
 * @description 根据taskId查询任务详情
 * @param id 指定id
 * */ 
export const useTask = (id?: number) => {
    const client = useHttp();
    return useQuery<Task>(
      ["task", { id }], // 此处的功能是id每次改变都会发送请求
      () => client(`tasks/${id}`),
      {
        enabled: Boolean(id), // id为undefined时不发送请求
      }
    );
};

/**
 * @description 编辑任务
 * @param queryKey 查询key
 * */ 
export const useEditTask = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        (params: Partial<Task>) => client(`tasks/${params.id}`, {
            method: 'PATCH',
            data: params,
        }),
        useEditConfig(queryKey)
    );
};

/**
 * @description 删除任务
 * @param queryKey 查询key
 * */ 
export const useDeleteTask = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        ({ id }: { id: number }) => client(`tasks/${id}`, {
            method: 'DELETE',
        }),
        useDeleteConfig(queryKey)
    );
}

export const useReorderTask = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation((params: SortProps) => {
      return client("tasks/reorder", {
        data: params,
        method: "POST",
      });
    }, useReorderTaskConfig(queryKey));
  };
  