export const Mark = ({name, keyword}: {name: string, keyword: string}) => {
    if (!keyword) {
        return <div>{name}</div>
    }
    const arr = name.split(keyword);
    return (
        <div>
            {
                arr.map((str: string, index: number) => <span key={index}>
                    { str }
                    {
                        index === arr.length - 1 ? null :
                        <span style={{color: 'red', fontWeight: 'bold'}}>
                            {keyword}
                        </span>
                    }
                </span>)
            }
        </div>
    )
}