import { useCallback, useMemo } from "react";
import { useLocation } from "react-router"
import { useDebounce } from "utils";
import { useProject } from "utils/project";
import { useTask } from "utils/task";
import { useUrlQueryParam } from "utils/url";

/**
 * @description 从url中获取projectId
 * */ 
export const useProjectIdInUrl = () => {
    const { pathname } = useLocation();
    const id = pathname.match(/projects\/(\d+)/)?.[1]; // match时，用括号包裹的表达式匹配的值会放在返回数组的第二个位置
    return Number(id);
}

/**
 * @description 根据projectId获取指定project详情
 * */ 
export const useProjectInUrl = () => useProject(useProjectIdInUrl());

/**
 * @description 构建看板列表查询所需参数
 * */ 
export const useKanbanSearchParams = () => ({projectId: useProjectIdInUrl()});

/**
 * @description 构建看板列表查询参数
 * */ 
export const useKanbansQueryKey = () => ["kanbans", useKanbanSearchParams()];

/**
 * @description 构建任务列表查询所需参数
 * */ 
export const useTasksSearchParams = () => {
    const [param, setParam] = useUrlQueryParam([
        'name', // task名称
        'typeId', // task类型
        'processorId', // 处理人id
        'tagId', // tagId
    ])
    const projectId = useProjectIdInUrl();
    // const debouncedName = useDebounce(param.name, 200);
    return useMemo(() => ({
        projectId,
        typeId: Number(param.typeId) || undefined,
        processorId: Number(param.processorId) || undefined,
        tagId: Number(param.tagId) || undefined,
        name: param.name,
    }), [projectId, param]);
}

/**
 * @description 构建任务列表查询参数
 * */ 
export const useTasksQueryKey = () => ['tasks', useTasksSearchParams()];

/**
 * @description 看板弹窗状态控制
 * */ 
export const useTaskModal = () => {
    const [{ editingTaskId }, setEditingTaskId] = useUrlQueryParam(['editingTaskId']);
    const { data: editingTask, isLoading } = useTask(Number(editingTaskId));
    const startEdit = useCallback((id: number) => {
        setEditingTaskId({editingTaskId: id});
    }, [setEditingTaskId]);
    const close = useCallback(() => {
        setEditingTaskId({editingTaskId: ''});
    }, [setEditingTaskId]);
    return {
        editingTaskId,
        editingTask,
        startEdit,
        close,
        isLoading,
    };
}