import { useMemo } from "react";
import { URLSearchParamsInit, useSearchParams } from "react-router-dom";
import { cleanObject } from "utils";

/**
 * @description 返回页面url中指定键名的参数值
 * @param {Array} 键名
 * @return [ {key1: value1}, () => setSearchParams(params) ]
 * */ 
export const useUrlQueryParam = <K extends string>(keys: K[]) => {
    const [searchParams] = useSearchParams(); // searchParams为URLSearchParams对象
    const setSearchParams = useSetUrlSearchParam();
    return [
        useMemo( // 使用useMemo依赖searchParams，当searchParams发生变化的时候再重新执行函数并返回，否则返回上一次的。
            () => {
                return keys.reduce((prev, key) => {
                    return  {...prev, [key]: searchParams.get(key) || ''};
                }, {} as { [key in K]: string });
            },
            // eslint-disable-next-line react-hooks/exhaustive-deps
            [searchParams],
        ),
        (params: Partial<{[key in K]: unknown}>) => { // 根据指定参数设置url的查询参数
            return setSearchParams(params);
        }
    ] as const;
};

/**
 * @description 设置url的查询参数
 * */ 
export const useSetUrlSearchParam = () => {
    const [searchParams, setSearchParam] = useSearchParams(); // setSearchParam 设置url查询参数
    return (params: { [key in string]: unknown }) => { // key in string 表示的是 key为字符串类型
      const o = cleanObject({
        ...Object.fromEntries(searchParams),
        ...params,
      }) as URLSearchParamsInit;
      return setSearchParam(o);
    };
};