import { QueryKey, useMutation, useQuery } from "react-query";
import { Kanban } from "types/kanban";
import { useHttp } from "./http";
import {
    useAddConfig,
    useDeleteConfig,
    useReorderKanbanConfig,
} from "./use-optimistic-options";

/**
 * @description 获取看板列表
 * @param param 查询参数
 * */ 
export const useKanbans = (param?: Partial<Kanban>) => {
    const client = useHttp(); // fetch请求
    return useQuery<Kanban[]>(['kanbans', param], () => client('kanbans', { data: param })); // 当param变化的时候会重新触发useQuery
}

/**
 * @description 添加看板
 * @param queryKey 查询key
 * */ 
export const useAddKanban = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        (params: Partial<Kanban>) => client('kanbans', {
            method: 'POST',
            data: params,
        }),
        useAddConfig(queryKey)
    );
}

/**
 * @description 删除看板
 * @param queryKey 查询key
 * */ 
export const useDeleteKanban = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        ({ id }: { id: number }) => client(`kanbans/${id}`, {
            method: 'DELETE',
        }),
        useDeleteConfig(queryKey)
    );
}

export interface SortProps {
    fromId: number; // 要重新排序的item
    referenceId: number; // 目标item
    type: 'before' | 'after'; // 放在目标item的前还是后
    fromKanbanId?: number;
    toKanbanId?: number;
}


export const useReorderKanban = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation((params: SortProps) => {
      return client("kanbans/reorder", {
        data: params,
        method: "POST",
      });
    }, useReorderKanbanConfig(queryKey));
  };
  