import styled from '@emotion/styled';
import {
    Button,
    Drawer,
    Form,
    Input,
    Spin,
} from "antd";
import { useForm } from "antd/lib/form/Form";
import { ErrorBox } from "components/lib";
import { UserSelect } from "components/user-select";
import { useEffect } from "react";
import { useAddProject, useEditProject } from "utils/project";
import { useProjectModal, useProjectsQueryKey } from "./util";

/**
 * @description 项目弹窗
 * */ 
export const ProjectModal = () => {
    const {
        projectModalOpen,
        close,
        editingProject,
        isLoading,
    } = useProjectModal();
    const useMutateProject = editingProject ? useEditProject : useAddProject; // 编辑或添加项目Hook
    const title = editingProject ? '编辑项目' : '创建项目';
    const {
        mutateAsync, // 这里之所以使用异步，是因为我们希望等到编辑/创建等操作完成之后再去关闭窗口
        error,
        isLoading: mutateLoading
    } = useMutateProject(useProjectsQueryKey());
    const [form] = useForm();
    const onFinish = (values: any) => { // Form提交回调
        mutateAsync({...editingProject, ...values}).then(() => {
            form.resetFields();
            close();
        });
    };
    const closeModal = () => { // Modal关闭回调
        form.resetFields();
        close();
    };
    useEffect(() => { 
        form.setFieldsValue(editingProject); // 编辑状态下初始化项目项目详情
    }, [editingProject, form]);

    return (
        // 这里的forceRender表示不管Modal显示隐藏都强制渲染
        <Drawer forceRender={true} onClose={closeModal} width={'100%'} visible={projectModalOpen}>
            <Container>
                {
                    isLoading ? <Spin size={'large'} /> : 
                    <div>
                        <h1>{title}</h1>
                        <ErrorBox error={error} />
                        {/* Input放在Form中，会自动添加value和onChange属性 */}
                        <Form form={form} layout={'vertical'} style={{width: '40rem'}} onFinish={onFinish}>
                            <Form.Item label={'名称'} name={'name'} rules={[{required: true, message: '请输入项目名'}]}>
                                <Input placeholder={'请输入项目名称'} />
                            </Form.Item>
                            <Form.Item label={'部门'} name={'organization'} rules={[{required: true, message: '请输入部门名'}]}>
                                <Input placeholder={'请输入部门名称'} />
                            </Form.Item>
                            <Form.Item label={'负责人'} name={'personId'}>
                                <UserSelect defaultOptionName={'负责人'}></UserSelect>
                            </Form.Item>
                            <Form.Item style={{ textAlign: 'right' }}>
                                <Button loading={mutateLoading} type={"primary"} htmlType={'submit'}>
                                    提交
                                </Button>
                            </Form.Item>
                        </Form>
                    </div>
                }
            </Container>
        </Drawer>
    )
};

const Container = styled.div`
flex-direction: column;
height: 80vh;
display: flex;
justify-content: center;
align-items: center;
`;