import { Project } from "types/project";
import { useHttp } from "./http";
import { 
    useQuery,
    useMutation,
    QueryKey,
} from 'react-query';
import { 
    useAddConfig,
    useDeleteConfig,
    useEditConfig
} from "./use-optimistic-options";

/**
 * @description 获取项目列表
 * @param {Object} param 查询条件
 * */ 
export const useProjects = (param?: Partial<Project>) => {
    const client = useHttp();
    return useQuery<Project[]>(
        ['projects', param], // 缓存中的查询数据对应的键名，当param变化的时候，会重新请求并更新缓存
        () => client('projects', { data: param })
    );
}

/**
 * @description 编辑项目
 * @param queryKey 查询key
 * */ 
export const useEditProject = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        (params: Partial<Project>) => client(`projects/${params.id}`, {
            method: 'PATCH',
            data: params,
        }),
        useEditConfig(queryKey)
    );
};

/**
 * @description 添加项目
 * @param queryKey 查询key
 * */ 
export const useAddProject = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        (params: Partial<Project>) => client('projects', {
            method: 'POST',
            data: params,
        }),
        useAddConfig(queryKey)
    );
}

/**
 * @description 删除项目
 * @param queryKey 查询key
 * */ 
export const useDeleteProject = (queryKey: QueryKey) => {
    const client = useHttp();
    return useMutation(
        ({id}: {id: number}) => client(`projects/${id}`, {
            method: 'DELETE',
        }),
        useDeleteConfig(queryKey)
    );
}

/**
 * @description 根据指定Id查询项目详情
 * @param id 指定id
 * */ 
export const useProject = (id?: number) => {
    const client = useHttp();
    return useQuery<Project>(
      ["project", { id }], // 此处的功能是id每次改变都会发送请求
      () => client(`projects/${id}`),
      {
        enabled: Boolean(id), // id为undefined时不发送请求
      }
    );
};
