import React, { ReactNode, useCallback } from "react";
import * as auth from 'auth-provider';
import { User } from "../types/user";
import { http } from "utils/http";
import { useMount } from "utils";
import { useAsync } from "utils/use-async";
import { FullPageErrorFallback, FullPageLoading } from "components/lib";
import { 
    useQueryClient, // 返回当前QueryClient实例
} from "react-query";

interface AuthForm {
    username: string;
    password: string;
}

/**
 * @description // 初始化用户（页面重新加载的时候）
 * */   
const bootstrapUser = async () => {
    let user = null;
    const token = auth.getToken();
    if (token) {
        const data = await http('me', {token});
        user = data.user;
    }
    return user; 
}
 
const AuthContext = React.createContext<{
    user: User | null,
    register: (form: AuthForm) => Promise<void>,
    login: (form: AuthForm) => Promise<void>,
    logout: () => Promise<void>,
} | undefined>(undefined);

AuthContext.displayName = 'AuthContext';

/**
 * @description 用户信息全局Provider
 * */ 
export const AuthProvider = ({children}: {children: ReactNode}) => {
    const {data: user, error, isLoading, isIdle, isError, run, setData: setUser} = useAsync<User | null>();
    const queryClient = useQueryClient();
    const login = (form: AuthForm) => auth.login(form).then(setUser); // point free
    const register = (form: AuthForm) => auth.register(form).then(setUser);
    const logout = () => auth.logout().then(() =>{ 
        setUser(null);
        queryClient.clear(); // 清除所有连接的缓存
    });
    useMount(
        useCallback(() => {
            run(bootstrapUser());
        },[]
    ));
    if (isIdle || isLoading) {
        return <FullPageLoading />
    }
    if (isError) {
        return <FullPageErrorFallback error={error} />
    }
    return <AuthContext.Provider children={children} value={{ user, login, register, logout }} />;
}

/**
 * @description 自定义Hook, 获取用户信息
 * */ 
export const useAuth = () => {
    const context = React.useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth必须要在AuthProvider中使用');
    }
    return context;
}