import { Kanban } from "types/kanban";
import { Task } from 'types/task';
import { useDeleteTask, useTasks } from "utils/task";
import { useTaskTypes } from "utils/task-type";
import { useKanbansQueryKey, useTaskModal, useTasksQueryKey, useTasksSearchParams } from "./util";
import taskIcon from 'assets/task.svg';
import bugIcon from 'assets/bug.svg';
import styled from "@emotion/styled";
import { Button, Card, Dropdown, Menu, Modal } from "antd";
import { CreateTask } from "./create-task";
import { Mark } from "components/mark";
import { useDeleteKanban } from "utils/kanban";
import { Row } from "components/lib";
import React from "react";
import { Drag, Drop, DropChild } from "components/drag-and-drop";
import { CloseCircleOutlined } from '@ant-design/icons';


/**
 * @description 任务Icon
 * @param {Number} id 任务id
 * */ 
const TaskTypeIcon = ({id}: {id: number}) => {
    const {data: taskTypes} = useTaskTypes();
    const name = taskTypes?.find(taskType => taskType.id === id)?.name;
    if (!name) {
        return null;
    }
    return <img alt={'task-icon'} src={name === 'task' ? taskIcon : bugIcon} />;
}

/**
 * @description 任务Card
 * @param {Task} task 任务对象
 * */ 
const TaskCard = ({ task }: { task: Task }) => {
    const { startEdit } = useTaskModal();
    const { name: keyword } = useTasksSearchParams();
    const { mutateAsync: deleteTask} = useDeleteTask(useTasksQueryKey());
    const startDelete = (event: any, id: number) => {
        event.stopPropagation();
        Modal.confirm({
            okText: '确定',
            cancelText: '取消',
            title: '确定删除任务吗？',
            onOk() {
                return deleteTask({id});
            }
        });
    }
    return (
        <Card
            hoverable
            style={{ marginBottom: '0.5rem', cursor: 'pointer' }}
            key={task.id}
            onClick={() => startEdit(task.id)}
        >   
            <div>
                <DeleteTaskIcon style={{ color: '#CDC9C9' }} onClick={(e) => startDelete(e, task.id)} />
                <Mark keyword={keyword} name={task.name}/>
            </div>
            <TaskTypeIcon id={task.typeId} />
        </Card>
    )
}

/**
 * @description 看板列组件
*  @param {Kanban} kanban 看板对象
 * */ 
export const KanbanColumn = React.forwardRef<HTMLDivElement, { kanban: Kanban }>(({kanban, ...props}, ref) => {
    const { data: allTasks } = useTasks(useTasksSearchParams());
    const tasks = allTasks?.filter(task => task.kanbanId === kanban.id);
    return (
        <Container ref={ref} {...props}>
            <Row between={true}>
                <h3>{kanban.name}</h3>
                <More kanban={kanban} key={kanban.id}></More>
            </Row>
            <TasksContainer>
                <Drop type={'ROW'} direction={'vertical'} droppableId={String(kanban.id)}>
                    <DropChild style={{minHeight: '5px'}}>
                        { 
                            tasks?.map((task, taskIndex) =>(
                                <Drag key={task.id} index={taskIndex} draggableId={`task${task.id}`}>
                                    <div>
                                    <   TaskCard task={task} key={task.id}/>
                                    </div>
                                </Drag>
                            ))
                        }
                    </DropChild>
                </Drop>
                <CreateTask kanbanId={kanban.id} />
            </TasksContainer>
        </Container>
    );
});

/**
 * @description 看板列删除按钮组件
 * @param {Kanban} kanban 看板对象
 * */ 
const More = ({kanban}: {kanban: Kanban}) => {
    const { mutateAsync } = useDeleteKanban(useKanbansQueryKey());
    const startDelete = () => {
        Modal.confirm({
            okText: '确定',
            cancelText: '取消',
            title: '确定删除看板吗？',
            onOk() {
                return mutateAsync({id: kanban.id});
            }
        });
    }
    const overlay = (
        <Menu>
            <Menu.Item>
                <Button type={'link'} onClick={startDelete}>
                    删除
                </Button>
            </Menu.Item>
        </Menu>
    )
    return (
        <Dropdown overlay={overlay}>
            <Button type={'link'}>...</Button>
        </Dropdown>
    );
}

export const Container = styled.div`
    min-width: 27rem;
    border-radius: 6px;
    background-color: rgb(244,245,247);
    display: flex;
    flex-direction: column;
    padding: 0.7rem 0.7rem 1rem;
    margin-right: 1.5rem;
`;

const TasksContainer = styled.div`
    overflow: scroll;
    flex: 1;
    ::-webkit-scrollbar {
        display: none;
    }
`;

const DeleteTaskIcon = styled(CloseCircleOutlined)`
    margin: -2rem;
    float: right;
`;