import { useQuery } from "react-query";
import { TaskType } from "types/task-type";
import { useHttp } from "./http";

/**
 * @description 获取看板列表
 * @param param 查询参数
 * */ 
export const useTaskTypes = () => {
    const client = useHttp(); // fetch请求
    return useQuery<TaskType[]>(['taskTypes'], () => // n秒之内如果有相同queryKey发送，那么ReactQuery会将这些相同query合并为一个请求进行发送
        client('taskTypes') // 当param变化的时候会重新触发useQuery
    );
}
