import { useCallback, useReducer, useState } from 'react';
import { useMountedRef } from 'utils';

interface State<D> { // 状态类型
    error: Error | null;
    data: D | null;
    stat: 'idle' | 'loading' | 'error' | 'success';
}

const defaultInitialState: State<null> = { // 默认状态
    stat: 'idle',
    data: null,
    error: null,
}

const defaultConfig = {
    throwOnError: false
};

const useSafeDispatch = <T>(dispatch: (...args: T[]) => void) => {
    const mountedRef = useMountedRef();
    return useCallback((...args: T[]) => (mountedRef.current ? dispatch(...args) : void 0), [dispatch, mountedRef]);
}

export const useAsync = <D>(initialState?: State<D>, initialConfig?: typeof defaultConfig) => { // 异步自定义Hook
    const config = {...defaultConfig, ...initialConfig};
    const [state, dispatch] = useReducer((state: State<D>, action: Partial<State<D>>) => ({...state, ...action}), { // 设置异步状态
        ...defaultInitialState,
        ...initialState,
    });
    const safeDispatch = useSafeDispatch(dispatch);
    const [retry, setRetry] = useState(() => () => {});

    const setData = useCallback( // 设置成功时的异步状态
        (data: D) => safeDispatch({ 
            data,
            stat: 'success',
            error: null,
        }), [safeDispatch]);
    const setError = useCallback( // 设置失败时的异步状态
        (error: Error) => safeDispatch({ 
            error,
            stat: 'error',
            data: null,
        }), [safeDispatch]);
    const run = useCallback(
        (promise: Promise<D>, runConfig?: {retry: () => Promise<D>}) => { // run用来触发异步请求
            if (!promise || !promise.then) {
                throw new Error('请传入 Promise 类型数据');
            }
            setRetry(() => () => {
                if (runConfig?.retry) {
                    run(runConfig?.retry(), runConfig);
                }
            });
            safeDispatch({stat: 'loading'});
            return promise.then((data) => {
                setData(data);
                return data;
            }).catch((error) => {
                setError(error);
                if (config.throwOnError) {
                    return Promise.reject(error);
                }
                return error;
            });
        }, [config.throwOnError, setData, setError, safeDispatch]
    );
  
    return {
        isIdle: state.stat === 'idle', // 是否未开始
        isLoading: state.stat === 'loading', // 是否请求中
        isError: state.stat === 'error', // 是否请求失败
        isSuccess: state.stat === 'success', // 是否请求成功
        run, // 触发异步请求
        setData, // 设置成功时的异步状态
        setError,
        retry, // retry被调用时，重新跑一遍run，让state重新刷新一遍
        ...state,
    };
}