import { useMemo } from "react";
import { useProject } from "utils/project";
import { useSetUrlSearchParam, useUrlQueryParam } from "utils/url";

/**
 * @description 获取项目列表查询条件
 * @returns {Array} [{name: value, personId: value}, setParam]
 * */  
export const useProjectsSearchParams = () => {
    const [param, setParam] = useUrlQueryParam(['name', 'personId']);
    return [
        useMemo(() => ({...param, personId: Number(param.personId) || undefined}), [param]),
        setParam,
    ] as const;
}

/**
 * @description 获取项目查询参数，用于react-query
 * @returns {Array} ['projects', params]
 * */ 
export const useProjectsQueryKey = () =>{
    const [params] = useProjectsSearchParams();
    return ['projects', params];
};

/**
 * @description 项目弹窗管理
 * @returns {Object} {projectModalOpen, open, close, startEdit, editingProject, isLoading}
 * */ 
export const useProjectModal = () => {
    const [{projectCreate}, setProjectCreate] = useUrlQueryParam([  // 获取创建项目相关
        'projectCreate'
    ]);
    const [{ editingProjectId }, setEditingProjectId] = useUrlQueryParam([ // 获取编辑项目相关
        "editingProjectId",
    ]);
    const setUrlParams = useSetUrlSearchParam(); // 获取设置url查询参数的函数
    const { data: editingProject, isLoading } = useProject(Number(editingProjectId)); // 获取项目详情
    const open = () => setProjectCreate({projectCreate: true}); // 打开弹窗
    const close = () => setUrlParams({projectCreate: '', editingProjectId: ''}); // 关闭弹窗
    const startEdit = (id: number) => setEditingProjectId({ editingProjectId: id }); // 开始编辑
    return {
        projectModalOpen: projectCreate === 'true' || Boolean(editingProjectId), // 是否打开项目弹窗
        open,
        close,
        startEdit,
        editingProject,
        isLoading,
    };
}